.app {
  text-align: center;
  background: linear-gradient(
    4.92deg,
    #2f1f40 4.18%,
    #1e2247 50%,
    #1c0532 73.22%,
    #140c21 95.07%
  );
  background: radial-gradient(
    100.93% 100.93% at 50.83% 0.66%,
    rgba(77, 0, 138, 0.85) 0%,
    #001355 49.08%,
    #000535 100%
  );
  color: #eee;
  min-height: 100vh;
  background-repeat: no-repeat;
}
