.staking {
  padding: 1rem 0px;
  max-width: 450px;
  margin: auto;
  // &__container {
  // }

  &__heading {
    text-transform: uppercase;
    font-weight: 900;
    font-size: 1.25rem;
  }
  &__tvl {
    display: flex;
    width: max-content;
    margin: auto;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: 0.5rem 1rem;
  }
  &__apy {
    display: flex;
    width: max-content;
    margin: auto;
    gap: 0.5rem;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: 0.5rem 1rem;
  }

  &__container {
    border-radius: 15px;
    background: linear-gradient(
      90deg,
      rgba(23, 44, 102, 0.3) 0%,
      rgba(24, 22, 82, 0.3) 100%
    );
    box-shadow: 0px 0px 30px rgba(149, 100, 211, 0.33);
    backdrop-filter: blur(8px);
    margin: 2rem 0;
    padding: 0;

    border-image-slice: 1;
    border-width: 2px;
    border-image-source: linear-gradient(45deg, #89d2f3, #a67cff);
  }
  &__selector {
    &--amount {
      padding: 0.5rem;
    }
    &--time {
      padding: 0.5rem;
    }
  }

  &__amount {
    width: 100%;
    border: none;
  }
  &__amount > input {
    border: 1px solid #4c5199;
    border-left: none;
    border-right: none;
  }

  &__amount--label {
    text-align: left !important;
  }

  &__selector__heading {
    background-color: #161838;
    border-top-right-radius: 15px;
    border-top-left-radius: 15px;
    border-bottom: 1px solid #4c5199;
    padding: 1rem;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 1.25rem;
  }

  &__my_balance {
    position: absolute;
    right: 0;
    margin: 0.2rem 0.5rem 0.2rem 0px !important;
    // margin-right: 0.5rem !important;
  }
  &__amount_input {
    display: flex;
    width: 100%;
    padding: 0.25rem !important;
    position: relative;
  }
  &__amount_input:hover {
    border: #89d2f3;
  }

  &__max_btn {
    border-top-left-radius: 0px !important;
    border-bottom-left-radius: 0px !important;
    background: transparent !important;
  }

  & .chakra-input__right-addon {
    background-color: transparent;
    // border: none;
    border: 1px solid #4c5199;
  }
  & .chakra-input__left-addon {
    border: 1px solid #4c5199;
    background-color: transparent;
  }
}

.gradient-2 {
  // background: linear-gradient(red, #181652) padding-box,
  //   linear-gradient(45deg, #89d2f3, #a67cff) border-box !important;
  // border-radius: 0rem;
  background: linear-gradient(90deg, #172c66 0%, #181652 100%) padding-box,
    linear-gradient(45deg, #89d2f3, #a67cff) border-box;
  background: linear-gradient(
    rgba(24, 22, 82, 0.3),
    rgba(24, 22, 82, 0.3)
  ) !important;
  border: 1px solid transparent;
  border: 1px solid #89d2f3;
  box-shadow: 0px 0px 31px rgb(149 100 211 / 50%);
  backdrop-filter: blur(8px);
}
